import React from 'react';
import logo from './l.png'
import logoHover from './l-hover.png';
import './Navigation.css';

const Navigation = () => {
    return (
        <div className="df f-j-c-space-between" style={{userSelect: "none", backgroundColor:"#120136", color: '#40bad5'}}>
            <div className="ml3 relative logo-dimensions">
                <img className='absolute overlay logo ma2' src={logo} alt='logo' />
                <img className='absolute overlay logo-hover ma2' src={logoHover} alt='logo' />
            </div>
            <h1 className='j-s-center ml4 mr1'>Leitner Seven</h1>
            <ul className='ma2 df f-a-i-center'>
                <li className="di mt1 ml4 mr1">About</li>
                <li className="di mt1 ml4 mr1">Stats</li>
                <li className="di mt1 ml4 mr1">Sign Out</li>
            </ul>
        </div>
    )
}

export default Navigation;