import React from 'react';
import Modal from '../Modal';
import Confirm from '../../Buttons/Confirm/Confirm';
import Cancel from '../../Buttons/Cancel/Cancel';
import '../Modals.css'

const DeleteDeckModal = ({ onClickCancel, onClickConfirm }) => {
    const background = "delete-modal-background";

    const title = "Delete Deck";

    const body = (
        <div className="max-dimensions df f-d-column f-j-c-start f3 tc">
            <div className="ma4 f-f-g-1 df f-d-column f-j-c-center">
                <strong>You're about to delete a deck. All cards belonging to it will be removed as well.</strong>
                <strong>Are you sure you want to delete the deck?</strong>
            </div>
        </div>
    );
    
    const buttons = [
        <Cancel onClickCancel={onClickCancel} />,
        <Confirm onClickConfirm={onClickConfirm} />
    ];

    return (
        <Modal 
            background={background}
            title={title}
            body={body}
            buttons={buttons}
        />
    );
}

export default DeleteDeckModal;