import React from 'react';
import './Modals.css'
import Title from '../Title/Title';
import Scollable from '../Scrollable/Scrollable';

const Modal = ({ title, body, buttons, background }) => {
    
    return (
        <div className="absolute overlay">
            <div className={`max-dimensions ${background}`}>
                <div className="modal modal-dimensions overflow-hidden shadow-4 df f-d-column f-j-c-space-between">
                    <Title title={title} />
                    <Scollable>
                        {body}
                    </Scollable>
                    <div className="modal-options shadow-2">
                        <div className="max-dimensions df f-d-row f-a-i-stretch f-j-c-space-between">
                            {
                                buttons.map((button, index) => (
                                    <div key={index} className="w-25">
                                        {button}
                                    </div>
                                ))
                            }
                        </div>
                    </div>
                </div>
            </div>
        </div> 
    )
}

export default Modal;