import React from 'react';
import Cancel from '../../Buttons/Cancel/Cancel';
import Save from '../../Buttons/Save/Save';
import Modal from '../Modal';
import '../Modals.css'


const EditCardModal = ({ onClickCancel, onClickSave, card }) => {
    const background = "edit-modal-background";

    const title = "Edit Card";

    const body = (
        <div className="df f-d-column f-f-g-1 f-j-c-center f3">
            <h3 className="ma4">{card.question}</h3> 
            <hr className="mh4"/>
            <strong className = "ma4">{card.answer}</strong>
        </div>
    );
    
    const buttons = [
        <Cancel onClickCancel={onClickCancel} />,
        <Save onClickSave={onClickSave} />
    ];

    return (
        <Modal 
            background={background}
            title={title}
            body={body}
            buttons={buttons}
        />
    );
}

export default EditCardModal;