import React from 'react';
import './MenuCard.css';
import View from '../Buttons/View/View';
import Edit from '../Buttons/Edit/Edit';
import Delete from '../Buttons/Delete/Delete';

const MenuCard = ({ onClickView, onClickEdit, onClickDelete, text, id }) => {
    
    return (
        <div className="card card-dimensions shadow-4 mh3 mt4 tc">
            <div className="max-dimensions default-content df f-d-column f-j-c-start">
                <div className="info-dimensions overflow-hidden pt4 ph2">
                    <h2>{text}</h2>
                </div>
            </div>
            <div className="hover-content max-dimensions df f-d-column f-j-c-end">
                <div className="background f-f-g-1"></div>
                <div className="card-options">
                    <div className="max-dimensions df f-d-row f-a-i-stretch">
                        <View id={id} onClickView={onClickView} />
                        <Edit id={id} onClickEdit={onClickEdit} />
                        <Delete id={id} onClickDelete={onClickDelete}/>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default MenuCard;