import React from 'react';
import Title from '../Title/Title'
import Scollable from '../Scrollable/Scrollable';

const Screen = ({ title, body, footer }) => {
    
    return (
        <div className="df f-d-column absolute overlay f-j-c-space-between">
            <Title title={title} />
            <Scollable>
                <div className='ph5 max-dimensions df f-d-column'>
                    {body}
                </div>
            </Scollable>
            {footer || <div />}
        </div>
    )
}

export default Screen;