import React from 'react';
import Cancel from '../../Buttons/Cancel/Cancel';
import Save from '../../Buttons/Save/Save';
import Modal from '../Modal'
import '../Modals.css'

const EditDeckModal = ({ onClickCancel, onClickSave, deck }) => {
    const background = "edit-modal-background";

    const title = "Edit Deck";

    const body = (
        <div className="max-dimensions df f-d-column f-j-c-start f3">
            <div className="ma4 f-f-g-1 df f-d-column f-j-c-center">
                <h3>{deck.name}</h3>
            </div>
        </div>
    );
        
    const buttons = [
            <Cancel onClickCancel={onClickCancel} />,
            <Save onClickSave={onClickSave} />
    ];

    return (
        <Modal 
            background={background}
            title={title}
            body={body}
            buttons={buttons}
        />
    );
}

export default EditDeckModal;