import React from 'react';
import MenuCard from '../../MenuCard/MenuCard';
import Add from '../../Buttons/Add/Add'
import Screen from '../Screen'

const Cards = ({ onClickView, onClickEdit, onClickDelete, onClickAdd, deck }) => {
    const title = `Deck: ${deck.name}`;
    
    const body = (
        <div className='df f-d-row f-f-g-1 f-a-i-center f-a-c-center f-w-wrap f-j-c-center pb4'>
            {
                deck.cards.map((card, index) => 
                    (<MenuCard key={index} id={index} onClickView={onClickView} onClickEdit={onClickEdit} onClickDelete={onClickDelete} text={card.question} />)
                )
            }        
        </div>
    )

    const footer = (
        <div className="shadow-2 df f-d-row f-j-c-end">
            <Add onClickAdd={onClickAdd}/>
        </div>
    )

    return (
        <Screen 
            title={title}
            body={body}
            footer={footer}
        />
    )
}

export default Cards;