import React from 'react';
import Cancel from '../../Buttons/Cancel/Cancel';
import Modal from '../Modal'
import '../Modals.css'
import Cards from '../../Screens/Cards/Cards';

const ViewCardModal = ({ onClickCancel, deckName, card }) => {
    const background = "view-modal-background";

    const title = `Deck: ${deckName}`;

    const body = (
        <div className="max-dimensions df f-d-column f-j-c-start f3">
            <h3 className="ma4">{card.question}</h3> 
            <hr className="mh4"/>
            <strong className="ma4">{card.answer}</strong>
        </div>
    );
        
    const buttons = [
            <Cancel onClickCancel={onClickCancel} />
    ];

    return (
        <Modal 
            background={background}
            title={title}
            body={body}
            buttons={buttons}
        />
    );
}

export default ViewCardModal;