import React from 'react';
import Start from '../Buttons/Start/Start'
import './SideMenu.css';

const SideMenu = ({ setSelectedItem, selectItem, onClickStart }) => {
    
    return (
        <div className="side-menu f-f-b-170px df f-d-column f-j-c-space-between">
            <ul onClick={selectItem}>
                <li onClick = {() => setSelectedItem('home')} className="menu-item pv3 ph4">Home</li>
                <li onClick = {() => setSelectedItem('decks')} className="menu-item pv3 ph4">Decks</li>
                <li onClick = {() => setSelectedItem('stats')} className="menu-item pv3 ph4">Stats</li>
                <li onClick = {() => setSelectedItem('about')} className="menu-item pv3 ph4">About</li>
            </ul>
            <div className="tc ph3 pv4 mh2">
                <Start onClickStart={onClickStart} />
            </div>
        </div>
    );
}

export default SideMenu;