import React from 'react';
import Cancel from '../../Buttons/Cancel/Cancel';
import Save from '../../Buttons/Save/Save';
import Modal from '../Modal'
import '../Modals.css'

const AddCardModal = ({ onClickCancel, onClickSave }) => {
    const background = "add-modal-background";

    const title = "Add Card";

    const body = (
        <div className="max-dimensions df f-d-column f-j-c-start f3">
            <h3 className="ma4">Some Question</h3> 
            <hr className="mh4"/>
            <strong className="ma4">Some Answer</strong>
        </div>
    );
        
    const buttons = [
            <Cancel onClickCancel={onClickCancel} />,
            <Save onClickSave={onClickSave} />
    ];

    return (
        <Modal 
            background={background}
            title={title}
            body={body}
            buttons={buttons}
        />
    );
}

export default AddCardModal;