import React from 'react';
import Start from '../../Buttons/Start/Start'
import Screen from '../Screen'

const About = () => {
    const title = "About";

    const body = (
        <div className='df f-d-column f-f-g-1 f-a-i-center f-a-c-center f-w-wrap pv4'>
            <h1 className="mb4">Hello USER,</h1>
            <h2>X cards due today</h2>
            <div className="mt5">
                <Start />
            </div>
        </div>
    )

    return (
        <Screen 
            title={title}
            body={body}
        />
    )

}

export default About;